@import url('https://fonts.googleapis.com/css2?family=Sofia+Sans+Condensed:wght@200;300;400;600&display=swap');

.stage {
    margin: auto;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    perspective: 9999px;
    transform-style: preserve-3d;
}

.layer {
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    transform-style: preserve-3d;
    animation: ಠ_ಠ 5s infinite alternate ease-in-out -7.5s;
    animation-fill-mode: forwards;
    transform: rotateY(40deg) rotateX(33deg) translateZ(0);
}

.layer:after {
    font-size: 50px;
    content: attr(data-text);
    white-space: pre;
    text-align: center;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 50px;
    left: 0;
    color: whitesmoke;
    letter-spacing: 4px;
    text-shadow: 4px 0 10px rgba(0, 0, 0, 0.13);
}

.layer:nth-child(1):after {
    transform: translateZ(0px);
}

.layer:nth-child(2):after {
    transform: translateZ(-1.5px);
}

.layer:nth-child(3):after {
    transform: translateZ(-3px);
}

.layer:nth-child(4):after {
    transform: translateZ(-4.5px);
}

.layer:nth-child(5):after {
    transform: translateZ(-6px);
}

.layer:nth-child(6):after {
    transform: translateZ(-7.5px);
}

.layer:nth-child(7):after {
    transform: translateZ(-9px);
}

.layer:nth-child(8):after {
    transform: translateZ(-10.5px);
}

.layer:nth-child(9):after {
    transform: translateZ(-12px);
}

.layer:nth-child(10):after {
    transform: translateZ(-13.5px);
}

.layer:nth-child(11):after {
    transform: translateZ(-15px);
}

.layer:nth-child(12):after {
    transform: translateZ(-16.5px);
}

.layer:nth-child(13):after {
    transform: translateZ(-18px);
}

.layer:nth-child(14):after {
    transform: translateZ(-19.5px);
}

.layer:nth-child(15):after {
    transform: translateZ(-21px);
}

.layer:nth-child(16):after {
    transform: translateZ(-22.5px);
}

.layer:nth-child(17):after {
    transform: translateZ(-24px);
}

.layer:nth-child(18):after {
    transform: translateZ(-25.5px);
}

.layer:nth-child(19):after {
    transform: translateZ(-27px);
}

.layer:nth-child(20):after {
    transform: translateZ(-28.5px);
}

.layer:nth-child(n+10):after {
    -webkit-text-stroke: 3px rgba(0, 0, 0, 0.25);
}

.layer:nth-child(n+11):after {
    -webkit-text-stroke: 5px dodgerblue;
    text-shadow: 6px 0 6px #00366b, 5px 5px 5px #002951, 0 6px 6px #00366b;
}

.layer:nth-child(n+12):after {
    -webkit-text-stroke: 5px #0077ea;
}

.layer:last-child:after {
    -webkit-text-stroke: 6px rgba(0, 0, 0, 0.1);
}

.layer:first-child:after {
    color: #fff;
    text-shadow: none;
}

@keyframes ಠ_ಠ {
    100% {
        transform: rotateY(-40deg) rotateX(-43deg);
    }
}

* *,
*::before,
*::after {
    animation-play-state: running !important;
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

body {
    -webkit-text-size-adjust: none;
}

mark {
    background-color: transparent;
    color: inherit;
}

input::-moz-focus-inner {
    border: 0;
    padding: 0;
}


/* Basic */

html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background: #D4D9DD url("/assets/system/img/bg03.jpg");
}

h1 {
    font-weight: 300 !important;
}

#page-wrapper, #comments .modal-body, #todo {
    color: #474f51;
    font-size: 13.5pt;
    font-family: 'Sofia Sans Condensed', sans-serif !important;
    font-weight: 400;
}

ul,
ol,
p,
dl {
    margin: 0 0 2em 0;
}

a {
    text-decoration: underline;
}

a:hover {
    text-decoration: none;
}

section> :last-child,
section:last-child,
.last-child {
    margin-bottom: 0 !important;
}

/* Container */

.container {
    margin: 0 auto;
    max-width: 100%;
    width: 1200px;
}

@media screen and (max-width: 1680px) {

    .container {
        width: 1200px;
    }

}

@media screen and (max-width: 1280px) {

    .container {
        width: calc(100% - 40px);
    }

}

@media screen and (max-width: 980px) {

    .container {
        width: calc(100% - 50px);
    }

}

@media screen and (max-width: 736px) {

    .container {
        width: calc(100% - 25px);
    }

}

